<template>
  <div @click="go" class="readme">
    <h1>С днем Рождения!</h1>
    <p>
      В этой игре тебе нужно будет отгадывать разные места и чекиниться в них (посещать)
    </p>
    <p>
      Место загадывается как одно слово или фраза "каникулы" или "то самое кафе"
    </p>
    <p>
      Если ты отгадала место и пришла в него, то
      <span class="red"></span> смениться на <span class="lightgreen"></span>
    </p>
    <p>
      За каждое место ты получаешь одно очко, или половину, если взяла для него подсказку.
    </p>
    <p>
      Все места в пределах СПБ. Не забудь одеться по спортивному и прихватить велосипед.
    </p>
    <p>
      И, да, время ограничено!
      <small>{{ moment | timer }}</small>
    </p>
    <p>
      P. S. Для продолжения КЛИК по экрану...
    </p>
  </div>
</template>

<script>
export default {
  data: () => ({
    moment : "",
    i: null
  }),
  computed: {
    readme(){
      return this.$store.state.readme
    }
  },
  filters: {
    timer( value){
      return value ? value.split(':').map( time => time.length < 2 ? '0' + time : time ).join(':') : ''
    }
  },
  watch: {
    readme( to , from){
      if ( to )
        this.$router.push({ name: 'main' })
    }
  },
  methods: {
    go(){
      this.$store.commit('read')
    },
    check(){
      const data = new Date()
      let h = data.getHours(), m = data.getMinutes(), s= data.getSeconds()

      if ( 15 - h < 0 )
        this.$router.push({ name: 'scope' })

      this.moment = `${15 - h}:${59 - m}:${59 - s}`
    }
  },
  mounted(){
    this.i = setInterval( this.check , 1000 )
  },
  beforeDestroy(){
    if ( this.i ) clearInterval( this.i )
  }
}
</script>

<style lang="stylus">
.readme
  margin 12px 12px
  .red
    background red
  .lightgreen
    background lightgreen
  span
    width 24px
    height 24px
    background #eee
    display inline-block
    border-radius 50%
</style>
