import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const distance = ([lat1, lon1], [lat2, lon2]) => {
  const p = 0.017453292519943295,   // Math.PI / 180
  c = Math.cos,
  a = 0.5 - c((lat2 - lat1) * p)/2 +
          c(lat1 * p) * c(lat2 * p) *
          (1 - c((lon2 - lon1) * p))/2;

  return 12742 * Math.asin(Math.sqrt(a)) // 2 * R; R = 6371 km
}

function sc(c, t){
  return c ? !t ? 1 : 0.5 : 0
}

const store = new Vuex.Store({
  state: {
    count: 0,
    readme: false,
    places: [
      {
        name: "дом",
        title: "дом",
        checkin: false,
        tip: false,
        x: 59.941582,y: 30.2317243
      },
      {
        name: "морра",
        title: "морра",
        checkin: false,
        tip: false,
        x: 59.935087, y: 30.2375013
      },
      {
        name: "нос ковалева",
        title: "нос",
        checkin: false,
        tip: false,
        x: 59.9249381, y: 30.3064302
      },
      {
        name: "дворик",
        title: "железный дровосек",
        checkin: false,
        tip: false,
        x: 59.9243884, y: 30.3428153
      },
      {
        name: "шахматы",
        title: "шахматы",
        checkin: false,
        tip: false,
        x: 59.9246429, y: 30.3401161
      },
      {
        name: "универ дурова",
        title: "вк",
        checkin: false,
        tip: false,
        x: 59.9402949, y: 30.29691
      },
      {
        name: "заяц спб",
        title: "деревянный безбилетник",
        checkin: false,
        tip: false,
        x: 59.9523974, y: 30.3221692
      },
      {
        name: "яндекс",
        title: "сру",
        checkin: false,
        tip: false,
        x: 59.9591025, y: 30.4039399
      },
      {
        name: "мармариса",
        title: "когда муж повел в кафе...",
        checkin: false,
        tip: false,
        x: 59.915849,y: 30.3154593
      },
      {
        name: "человек невидимка",
        title: "Шабуров шалит",
        checkin: false,
        tip: false,
        x: 59.9178608,y: 30.3011879
      },
      {
        name: "мини-город",
        title: "Исаакий для малышек",
        checkin: false,
        tip: false,
        x: 59.9549608,y: 30.3160697
      }
    ]
  },
  mutations: {
    read(state){
      state.readme = true
    },
    tipme (state, [payload , result]) {
      if (
        result &&
        state.places[payload] && !state.places[payload].checkin
      ) {
        state.places[payload].tip = result
      }
    },
    loc( state, [x, y] ){
      state.places = state.places.map( place => {
        if ( !place.checkin ) {
          if ( distance([place.x, place.y], [x,y]) < .3 )
            place.checkin = true
        }

        return place
      })
    },
    load(state, payload){
      state.places = state.places.map( ( place, i  ) => {
        const { checkin, tip } = payload[i]
        return { ...place, checkin , tip }
      })
    }
  },
  getters: {
    checkins(state){
      return state.places.reduce( ( value, { checkin, tip } ) =>
        value + sc( checkin, tip ) , 0
      )
    },
    perfect(state){
      return JSON.stringify(state.places.map(({checkin, tip}) => ({checkin, tip})))
    }
  }
})

export default store
