import Vue from 'vue'
import VueRouter from 'vue-router'

import Main from './components/Main.vue'
import Scope from './components/Scope.vue'
import Help from './components/Help.vue'


Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'main',
    component: Main
  },
  {
    path: '/scope',
    name: 'scope',
    component: Scope
  },
  {
    path: '/readme',
    name: 'readme',
    component: Help
  },
  //{ path: '/bar', component: Bar }
]

export default new VueRouter({
  routes
})
